<style lang="stylus" scoped>
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	form
		padding 1.5rem
		h1
			margin-bottom 1.5rem
			font-size 1.25rem
			line-height 1.5
			border-bottom 1px solid $black1
			color $black1
	.btn
		margin-top .625rem
		margin-bottom @margin-top
	p
		text-align right
		font-size .75rem
	.anchor
		color rgb(48, 96, 255)
		transition color .25s
		&:hover
			color red
</style>
<template>
	<form>
		<h1>注册</h1>
		<my-input type="user" :middleware="middleware" ref="user" />
		<my-input type="password" ref="password" />
		<a class="btn" @click="submit">注册</a>
		<p>
			<span>已经拥有账号？赶紧</span><router-link class="anchor" to="/signIn">登录</router-link>
		</p>
	</form>
</template>
<script>
	import MyInput from "../components/Input";
	export default {
		data(){
			return {
				user : "",
				password : "",
				message : "",
				async middleware(user){
					return (await fetch(`/api/checkUser?user=${user}`)).json();
				}
			};
		},
		methods : {
			async submit(){
				const {
					user,
					password
				} = this.$refs;
				if(user.validate() && await user.handleCheck() && password.validate()){
					try{
						const {
							code,
							message
						} = await (await fetch("/api/signUp", {
							method : "post",
							headers : {
								"Content-Type" : "application/x-www-form-urlencoded"
							},
							credentials : "include",
							body : `user=${this.$refs.user.value}&password=${this.$refs.password.value}`
						})).json();
						if(code){
							return this.$parent.$refs.dialog.message = message;
						}
						this.$router.push("/me");
					}catch(e){
						this.$parent.$refs.dialog.message = "网络错误";
					}
				}
			}
		},
		components : {
			MyInput
		},
		beforeCreate(){
			this.$parent.footer = 0;
		}
	};
</script>


