<style lang="stylus" scoped>
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	.ipt-txt
		margin-top .625rem
		width 100%
		height 2.5rem
		input
			box-sizing border-box
			padding-right .625rem
			padding-left @padding-right
			width inherit
			height @width
			border none
			font-size .875rem
			border 1px solid $grey4
			transition box-shadow .25s
			&:focus
				border-color $grey4
		&.error
			input
				border-color red
</style>
<template>
	<div :class="{'ipt-txt':1,error:dialog?dialog.message:0}">
		<input v-model="value" :placeholder="this.placeholder" @input="validate" @change="handleCheck" />
	</div>
</template>
<script>
	const Validate = {
		tel : {
			label : "手机号",
			regExp : /^1[3578]\d{9}$/,
			message(){
				return `${this.label}格式输入有误`;
			}
		},
		user : {
			label : "用户名",
			regExp : /^\S+$/,
			message(){
				return `${this.label}不能包含空格`;
			}
		},
		password : {
			label : "密码",
			regExp : /^\S{6,16}$/,
			message(){
				return `${this.label}必须为6至16位非空字符`;
			}
		}
	};
	export default {
		props : ["type", "middleware", "dialog"],
		data(){
			return {
				value : ""
			};
		},
		computed : {
			placeholder(){
				return `请输入${Validate[this.type].label}`;
			}
		},
		methods : {
			validate(){
				const type = Validate[this.type],
					isValid = +type.regExp.test(this.value);
				this.$parent.$parent.$refs.dialog.message = [type.message(), ""][isValid];
				return isValid;
			},
			async handleCheck(){
				if(typeof this.middleware === "function"){
					try{
						const {
							code,
							message
						} = await this.middleware(this.value);
						this.$parent.$parent.$refs.dialog.message = [message, ""][code];
						return code;
					}catch(e){
						this.$parent.$parent.$refs.dialog.message = "网络错误";
					}
				}
			}
		}
	};
</script>


